<script>import { onMount, setContext } from 'svelte';
import { writable } from 'svelte/store';
import { theme, } from './theme';
export let initialThemeType = 'light';
export let customTheme = {};
const themes = {
    light: Object.assign(Object.assign({}, theme.light), customTheme === null || customTheme === void 0 ? void 0 : customTheme.light),
    dark: Object.assign(Object.assign({}, theme.dark), customTheme === null || customTheme === void 0 ? void 0 : customTheme.dark),
};
const darkSchemeQuery = window.matchMedia('(prefers-color-scheme: dark)');
if (darkSchemeQuery.matches)
    initialThemeType = 'dark';
const store = writable({
    themeType: initialThemeType,
    theme: themes[initialThemeType],
    changeThemeType: () => { },
});
const setCssVars = (current) => {
    Object.entries(current).forEach(([type, hex]) => {
        const kebabize = (str) => str
            .replace(/([a-z0-9])([A-Z])/g, '$1-$2')
            .replace(/([A-Z])([A-Z])(?=[a-z])/g, '$1-$2')
