        return Object.assign(Object.assign({}, $store), { themeType: newThemeType, theme: themes[newThemeType] });
    });
};
setContext('svelte-theme', {
    theme: $store.theme,
    themeType: $store.themeType,
    changeThemeType,
});
onMount(() => {
    setCssVars($store.theme);
});
</script>

<slot />
